import { motion } from "motion/react";

export default function Experience() {
  return (
    <div
      className="experience vh-100 col-lg-9 m-3 d-flex flex-column justify-content-evenly align-content-center"
      id="experience"
    >
      <motion.div
        whileInView={{ opacity: 1, x: 0 }}
        initial={{ opacity: 0, x: -100 }}
        transition={{ duration: 0.5 }}
      >
        <h1 className="text-light">Experience</h1>
      </motion.div>
      <motion.div
        whileInView={{ opacity: 1, x: 0 }}
        initial={{ opacity: 0, x: -100 }}
        transition={{ duration: 1 }}
      >
        <h3 className="text-light">Internship</h3>
        <div>
          <h4 className="text-light">React JS Developer Intern</h4>
          <div className="d-flex justify-content-between">
            <span>Bluekode Academy</span>
            <span>18/07/2024 - present</span>
          </div>
        </div>
      </motion.div>
      <motion.div
        whileInView={{ opacity: 1, x: 0 }}
        initial={{ opacity: 0, x: -100 }}
        transition={{ duration: 1 }}
      >
        <ul className="py-4 d-flex flex-column align-items-center justify-content-center">
          <li>
            Building responsive, user-centric web applications using React.js,
            JavaScript, HTML, and CSS, with seamless designs implemented through
            Bootstrap and Tailwind CSS.
          </li>
          <li>
            Translating design concepts into optimized, functional web
            components while ensuring cross-browser compatibility and
            performance optimization.
          </li>
          <li>
            Utilizing React’s advanced features to boost application scalability
            and speed, delivering efficient and maintainable code aligned with
            best practices.
          </li>
          <li>
            Continuously enhancing skills and workflows by exploring emerging
            technologies and modern front-end development methodologies.
          </li>
        </ul>
      </motion.div>
    </div>
  );
}
