import { motion } from "motion/react";
import html from "../images/html-5.svg";
import css from "../images/css-alt.svg";
import javascript from "../images/javascript.svg";
import react from "../images/react.svg";
import tailwind from "../images/tailwind-css.svg";
import bootstrap from "../images/bootstrap.svg";
import jquery from "../images/jquery-4.svg";

export default function Skills() {
  const skillData = [
    {
      altName: "HTML",
      src: html,
    },
    {
      altName: "CSS",
      src: css,
    },
    {
      altName: "Javascript",
      src: javascript,
    },
    {
      altName: "React Js",
      src: react,
    },
    {
      altName: "JQuery",
      src: jquery,
    },
    {
      altName: "Bootstrap",
      src: bootstrap,
    },
    {
      altName: "Tailwind",
      src: tailwind,
    },
  ];
  return (
    <div
      className="skills vh-100 col-11 d-flex flex-column justify-content-center align-content-center m-3"
      id="skills"
    >
      <h1 className="text-light text-start">Skills</h1>
      <div className="program ">
        <div className="d-flex flex-wrap gap-5 my-5 justify-content-center align-items-center">
          {skillData.map((skill, index) => (
            <motion.img
              initial={{ x: -10, opacity: 0 }}
              whileInView={{ x: 0, opacity: 1 }}
              layout
              transition={{ duration: 0.5, delay: index * 0.2, ease: "linear" }}
              className="col-4 m-auto col-md-3 col-lg-2"
              src={skill.src}
              alt={skill.altName}
              key={index}
            />
          ))}
        </div>
      </div>
    </div>
  );
}
