export default function Header() {
  const linkData = [
    {
      linkHref: "hero",
      iconName: "bi-house-fill",
      navText: "Home",
    },
    {
      linkHref: "about",
      iconName: "bi-person-circle",
      navText: "About",
    },
    {
      linkHref: "experience",
      iconName: "bi-briefcase-fill",
      navText: "Experience",
    },
    {
      linkHref: "skills",
      iconName: "bi-trophy-fill",
      navText: "Skills",
    },
    {
      linkHref: "contact",
      iconName: "bi-envelope-fill",
      navText: "Contact",
    },
  ];
  return (
    <nav
      id="navbar-example2"
      className="navbar bg-primary px-lg-3 col-2 col-md-1 col-lg-2"
    >
      <ul className="nav flex-lg-column">
        {linkData.map((link, index) => (
          <li className="nav-item" key={index}>
            <a className="nav-link " href={`#${link.linkHref}`}>
              <span className="nav-icon">
                <i className={`bi ${link.iconName}`}></i>
              </span>
              <span className="nav-text">{link.navText}</span>
            </a>
          </li>
        ))}
      </ul>
    </nav>
  );
}
