import { motion } from "motion/react";
import { ReactTyped } from "react-typed";
import resume from "../assets/Aswin-B-React-Js-Developer.pdf";
import profile from "../images/aswin.jpg";

export default function Hero() {
  return (
    <div
      className="home vh-100 d-flex flex-column flex-lg-row justify-content-evenly justify-content-md-evenly align-items-center "
      id="hero"
    >
      <div className="head col-10 col-md-7 text-center mt-5 mt-lg-1 text-sm-start align-self-center">
        <motion.h1
          initial={{ x: 50, opacity: 0 }}
          whileInView={{ x: 0, opacity: 1 }}
          layout
          transition={{ duration: 1, delay: 0, ease: "linear" }}
          className="text-light"
        >
          Hi!
        </motion.h1>
        <motion.h1
          className="text-light"
          initial={{ x: 50, opacity: 0 }}
          whileInView={{ x: 0, opacity: 1 }}
          layout
          transition={{ duration: 1, delay: 0.1, ease: "linear" }}
        >
          I'm{" "}
          <ReactTyped
            className="title fs-1"
            strings={["Aswin"]}
            typeSpeed={60}
          />
        </motion.h1>
        <ReactTyped
          className="title fs-1"
          strings={[
            "React JS Developer",
            "Front End Developer",
            "Web Developer",
          ]}
          typeSpeed={50}
          backSpeed={40}
          startDelay={1000}
          loop
        />
        <div className="d-flex justify-content-between justify-content-md-start align-items-center my-3">
            <motion.a
              href={resume}
              download="resume"
              className="text-success text-decoration-none my-2 download bg-light px-1 px-lg-5 py-1 py-lg-2 rounded "
              initial={{ x: -100, opacity: 0 }}
              whileInView={{ x: 0, opacity: 1 }}
              layout
              transition={{ duration: 1, delay: 0.5, ease: "linear" }}
            >
              <span className="title mx-2">Download Resume</span>
              <span className="title">
                <i class="bi bi-download mx-2 fw-bold"></i>
              </span>
            </motion.a>
          {/* </button> */}
        </div>
      </div>
      <div className="col-10 col-md-8 col-lg-5">
        <img
          src={profile}
          alt="profile-picture"
          height="400"
          width="300"
          className="img-fluid m-auto portrait w-100"
        />
      </div>
    </div>
  );
}
