import { motion } from "motion/react";
export default function About() {
  return (
    <motion.div
      initial={{ x: -50, opacity: 0 }}
      whileInView={{ x: 0, opacity: 1 }}
      layout
      transition={{ duration: 0.25, delay: 0.4, ease: "linear" }}
    >
      <div
        className="about vh-100 col-lg-9 d-flex flex-column justify-content-center align-content-center m-3"
        id="about"
      >
        <h1 className="text-light">About</h1>
        <p>
          <b className="text-success">Hi I'm Aswin</b>, I am a Front-End
          Developer with a strong track record of creating seamless and engaging
          web experiences. My ability to understand user needs and translate
          them into functional designs has consistently contributed to
          successful project outcomes. With a commitment to excellence, I focus
          on delivering solutions that are both effective and user-centric.
        </p>
        <p>
          Currently, as a React.js Developer Intern, I am gaining valuable
          experience by working on real-world projects, enhancing my
          problem-solving skills, and refining my attention to detail. I take
          pride in my ability to collaborate effectively, meet deadlines, and
          continuously improve through feedback and learning.
        </p>
      </div>
    </motion.div>
  );
}
